* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: hsl(216deg, 12%, 8%);
  color: hsl(0deg, 0%, 100%);
  height: 100vh;
  font-family: "Overpass", sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
}

.bg {
  position: absolute;
  inset: 0;
  border-radius: inherit;
  backface-visibility: hidden;
  background: linear-gradient(to bottom, #202937 0%, hsl(215deg, 26%, 13%));
}

.bottom-bg {
  background-color: hsl(204deg, 29%, 18%);
  border-radius: 50%;
  width: 38px;
  height: 38px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.bottom-bg--item {
  width: 40px;
  height: 40px;
}

.sr-only {
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  display: block;
  border: 0;
}

.card {
  background-color: hsl(216deg, 12%, 8%);
  width: 90%;
  max-width: 320px;
  padding: 20px;
  border-radius: 20px;
  height: 320px;
  position: relative;
}

.front {
  transform: perspective(300px) rotateY(0deg);
  transition: transform 1s linear, opacity 1s linear;
}
.front--active {
  transform: perspective(300px) rotateY(180deg);
}

.face {
  padding: 25px 36px 25px 25px;
}
.face__img--svg {
  width: 13px;
}

.description {
  margin-top: 25px;
}
.description__heading {
  font-size: 22px;
  font-weight: 400;
}
.description__text {
  margin-top: 8px;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.5;
  color: hsl(217deg, 8%, 55%);
}

.options {
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
}
.options__item {
  font-size: 12px;
  padding: 12px;
  color: hsl(217deg, 8%, 55%);
  text-decoration: none;
  transition: background-color 0.3s linear, color 0.3s linear;
  cursor: pointer;
}
.options__item:hover {
  background-color: hsl(25deg, 97%, 53%);
  outline: none;
  color: hsl(0deg, 0%, 100%);
}
.options__input {
  margin-top: 20px;
  width: 100%;
  padding: 10px;
  border: none;
  border-radius: 20px;
  background-color: hsl(25deg, 97%, 53%);
  color: hsl(0deg, 0%, 100%);
  cursor: pointer;
  letter-spacing: 2px;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 700;
  transition: background-color 0.3s linear, color 0.3s linear;
}
.options__input:hover {
  color: hsl(25deg, 97%, 53%);
  background-color: hsl(0deg, 0%, 100%);
}

.checkbox {
  display: none;
}
.checkbox:checked ~ .options__item {
  background-color: hsl(217deg, 8%, 55%);
  color: #fff;
}

.disabled {
  cursor: no-drop;
}

.back {
  transform: perspective(300px) rotateY(180deg);
  transition: transform 1s linear, opacity 1s linear;
}
.back--active {
  transform: perspective(300px) rotateY(360deg);
  transition: transform 1s linear, opacity 1s linear;
}

.back-card {
  padding: 40px 25px;
  text-align: center;
}
.back-card__img {
  width: 125px;
}

.selected {
  margin-top: 20px;
}
.selected__text {
  width: 60%;
  margin: 0 auto;
  font-size: 12px;
  background-color: hsl(204deg, 29%, 18%);
  color: hsl(25deg, 97%, 53%);
  padding: 5px;
  border-radius: 15px;
  font-weight: 400;
}

.gratitude {
  margin-top: 20px;
}
.gratitude__heading {
  font-size: 21px;
  font-weight: 400;
}
.gratitude__text {
  margin-top: 10px;
  font-size: 12px;
  color: hsl(217deg, 8%, 55%);
  line-height: 1.6;
}